/* CSS Variables */
/* Variables */
/* Site Height */
$--navigation-height: 50px;
$--footer-height: $--navigation-height;
$--top-height: 0px;
$--bottom-height: 0px;
/* Background */
$--bg-darker-1: 0.7;
$--bg-darker-2: 0.9;
/* Text */
$--font-default: 12px;
/* SVG colors */
/* Rose-Gold */
$--svg-rose-gold-soft: invert(92%) sepia(38%) saturate(4291%) hue-rotate(297deg) brightness(104%) contrast(95%);
$--svg-rose-gold-mild: invert(96%) sepia(78%) saturate(4836%) hue-rotate(299deg) brightness(98%) contrast(94%);
$--svg-rose-gold-dark: invert(68%) sepia(54%) saturate(378%) hue-rotate(335deg) brightness(95%) contrast(87%);
/* Gold Royal */
$--svg-gold-royal-soft: invert(78%) sepia(93%) saturate(324%) hue-rotate(332deg) brightness(93%) contrast(85%);
$--svg-gold-royal-mild: invert(100%) sepia(27%) saturate(4996%) hue-rotate(318deg) brightness(88%) contrast(93%);
$--svg-gold-royal-dark: invert(72%) sepia(18%) saturate(1160%) hue-rotate(5deg) brightness(89%) contrast(87%);
/* Golden Blond */
$--svg-golden-blond-soft: invert(82%) sepia(43%) saturate(391%) hue-rotate(340deg) brightness(98%) contrast(93%);
$--svg-golden-blond-mild: invert(96%) sepia(60%) saturate(1051%) hue-rotate(307deg) brightness(86%) contrast(97%);
$--svg-golden-blond-dark: invert(93%) sepia(58%) saturate(5649%) hue-rotate(303deg) brightness(88%) contrast(74%);
/* Crismon Dusk */
$--svg-crismon-dusk-soft: invert(38%) sepia(92%) saturate(548%) hue-rotate(296deg) brightness(80%) contrast(81%);
$--svg-crismon-dusk-mild: invert(16%) sepia(50%) saturate(3540%) hue-rotate(330deg) brightness(96%) contrast(94%);
$--svg-crismon-dusk-dark: invert(15%) sepia(19%) saturate(7303%) hue-rotate(330deg) brightness(88%) contrast(98%);
/* Death Red */
$--svg-death-red-soft: invert(19%) sepia(43%) saturate(5297%) hue-rotate(352deg) brightness(91%) contrast(89%);
$--svg-death-red-mild: invert(15%) sepia(77%) saturate(3155%) hue-rotate(355deg) brightness(94%) contrast(89%);
$--svg-death-red-dark: invert(15%) sepia(44%) saturate(4518%) hue-rotate(348deg) brightness(85%) contrast(94%);
/* Hot Summer Sun */
$--svg-summer-sun-soft: invert(41%) sepia(96%) saturate(699%) hue-rotate(358deg) brightness(97%) contrast(107%);
$--svg-summer-sun-mild: invert(31%) sepia(93%) saturate(2543%) hue-rotate(19deg) brightness(98%) contrast(101%);
$--svg-summer-sun-dark: invert(16%) sepia(57%) saturate(5297%) hue-rotate(19deg) brightness(98%) contrast(102%);
/* Summer Orange */
$--svg-summer-orange-soft: invert(73%) sepia(8%) saturate(2976%) hue-rotate(338deg) brightness(96%) contrast(108%);
$--svg-summer-orange-mild: invert(84%) sepia(19%) saturate(7475%) hue-rotate(327deg) brightness(98%) contrast(93%);
$--svg-summer-orange-dark: invert(59%) sepia(43%) saturate(1042%) hue-rotate(330deg) brightness(101%) contrast(101%);
/* Orange Coral */
$--svg-orange-coral-soft: invert(97%) sepia(85%) saturate(6967%) hue-rotate(298deg) brightness(107%) contrast(112%);
$--svg-orange-coral-mild: invert(93%) sepia(99%) saturate(3847%) hue-rotate(303deg) brightness(98%) contrast(109%);
$--svg-orange-coral-dark: invert(88%) sepia(30%) saturate(3025%) hue-rotate(318deg) brightness(100%) contrast(102%);
/* Orange Crushed */
$--svg-orange-crushed-soft: invert(62%) sepia(77%) saturate(595%) hue-rotate(338deg) brightness(102%) contrast(100%);
$--svg-orange-crushed-mild: invert(63%) sepia(31%) saturate(1752%) hue-rotate(339deg) brightness(102%) contrast(103%);
$--svg-orange-crushed-dark: invert(61%) sepia(89%) saturate(3996%) hue-rotate(1deg) brightness(105%) contrast(106%);
/* Winter Wonderland */
$--svg-winter-wonderland-soft: invert(94%) sepia(2%) saturate(5843%) hue-rotate(189deg) brightness(99%) contrast(87%);
$--svg-winter-wonderland-mild: invert(77%) sepia(13%) saturate(369%) hue-rotate(179deg) brightness(95%) contrast(87%);
$--svg-winter-wonderland-dark: invert(83%) sepia(7%) saturate(1169%) hue-rotate(182deg) brightness(89%) contrast(77%);
/* Night Sky */
$--svg-night-sky-soft: invert(16%) sepia(30%) saturate(3083%) hue-rotate(201deg) brightness(93%) contrast(89%);
$--svg-night-sky-mild: invert(20%) sepia(11%) saturate(5325%) hue-rotate(203deg) brightness(87%) contrast(105%);
$--svg-night-sky-dark: invert(10%) sepia(91%) saturate(2176%) hue-rotate(240deg) brightness(94%) contrast(97%);
/* Shades Of Grey */
$--svg-shades-of-grey-softer: invert(72%) sepia(4%) saturate(0%) hue-rotate(345deg) brightness(86%) contrast(89%);
$--svg-shades-of-grey-soft: invert(49%) sepia(1%) saturate(1609%) hue-rotate(22deg) brightness(93%) contrast(75%);
$--svg-shades-of-grey-mild: invert(33%) sepia(1%) saturate(0%) hue-rotate(31deg) brightness(96%) contrast(102%);
$--svg-shades-of-grey-dark: invert(16%) sepia(6%) saturate(16%) hue-rotate(360deg) brightness(89%) contrast(85%);
$--svg-shades-of-grey-darker: invert(0%) sepia(8%) saturate(5124%) hue-rotate(319deg) brightness(100%) contrast(87%);
/* Dark Blue Sea */
$--svg-dark-blue-sea-soft: invert(35%) sepia(9%) saturate(2612%) hue-rotate(148deg) brightness(94%) contrast(94%);
$--svg-dark-blue-sea-mild: invert(20%) sepia(17%) saturate(1255%) hue-rotate(167deg) brightness(94%) contrast(96%);
$--svg-dark-blue-sea-dark: invert(9%) sepia(6%) saturate(6877%) hue-rotate(212deg) brightness(94%) contrast(88%);
/* Root defaults */
/* Toolbar */
.css-hyum1k-MuiToolbar-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* Card Actions */
.css-dnrpxu-MuiCardActions-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* Link */
a {
  min-width: auto !important;
  padding: 0 !important;
}
/* Horizontal line styles */
hr {
  border: 1px inset rgba(171, 150, 111, 0.5);
  margin: 5vw;
  width: 100%;
  /* Make horizontal line invisible */
  &.space {
    margin: 2.5vw;
    opacity: 0;
  }
  /* Make horizontal line as appbar link mediator */
  &.mediator {
    width: 100%;
    opacity: 0;
    margin: 0;
  }
}
/* HMTL element */
html {
  background-image: url("../assets/image/background/site-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  /* Body element */
  body {
    background: linear-gradient(
        rgba(0, 0, 0, $--bg-darker-1),
        rgba(0, 0, 0, $--bg-darker-1)
      ),
      rgba(65, 65, 65, 0.65);
    justify-content: center;
    min-width: 400px;
    margin: 0;
  }
  /* Body class*/
  .body {
    justify-content: center;
    /* Side Margin: Margin - Body - Margin */
    &Margin {
      border: none;
    }
    /* Upper body element. Content area + Left and Right columns */
    &Column {
      border-right: 1.5px double rgb(155, 155, 155);
      border-left: 1.5px double rgb(155, 155, 155);
      background: rgba(65, 65, 65, 0.35);
      min-height: calc(
        100vh - $--navigation-height - $--top-height - $--bottom-height -
          $--footer-height
      );
      position: relative;
      max-width: 1920px;
      padding: 0;
      /* Content area */
      .bodyContent {
        justify-content: flex-start;
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
        display: flex;
        width: 100%;
        /* Edit mode */
        &Edit {
          @extend .bodyContent;
          opacity: 0.6;
        }
        /* Page Header */
        img.header {
          height: calc(360px - $--navigation-height - $--top-height);
          margin: 0 0 calc((3vh + 17px) * -1) 0 !important;
          border: 5px ridge rgba(171, 150, 111, 1);
          object-position: 20% 100%;
          object-fit: cover;
          border-radius: 0;
          width: 100%;
        }
        video.header {
          height: calc(360px - $--navigation-height - $--top-height);
          border: 5px ridge rgba(171, 150, 111, 1);
          margin: 0 0 -2.5px 0 !important;
          object-position: 50% 15%;
          object-fit: cover;
          border-radius: 0;
          z-index: 1049;
          width: 100%;
        }
        /* Top Right Corner Menu */
        .dotsHeader {
          position: absolute;
          z-index: 1050;
          height: 25px;
          width: 25px;
          right: 10px;
          top: 5px;
          /* Menu Icon */
          & svg {
            filter: $--svg-summer-sun-mild;
            height: 25px;
            width: 25px;
            /* Hover */
            &:hover {
              filter: $--svg-summer-sun-dark;
              cursor: pointer;
            }
          }
          /**/
          & div.menu {
            border: 1.5px double rgba(155, 155, 155, 1);
            background: rgba(65, 65, 65, 1);
            position: absolute;
            right: 4.165px;
            top: 16.67px;
            padding: 0;
            /**/
            & button {
              min-width: min-content;
              padding: 9px 12px;
              &:hover {
                background: rgb(87, 87, 87);
              }
            }
          }
        }
        /* Top Settings For Content */
        & .content {
          justify-content: center;
          align-items: center;
          display: flex;
          width: 100%;
          /* ContentColumn. Content Area Whole Page Height */
          &Column {
            @extend .content;
            flex-direction: column;
          }
          /* ContentRow. Content Area For Row Of Elements */
          &Row {
            @extend .content;
            margin: 0 !important;
            /* Editing Toolbar */
            & > div.toolbar {
              border: 1px solid rgba(155, 155, 155, 1);
              height: 30px;
              width: 100%;
              /* Editing Toolbar Icons */
              & > svg {
                filter: $--svg-gold-royal-dark;
                padding: 0 5px !important;
                height: 25px;
                width: 25px;
                /* Hover */
                &:hover {
                  filter: $--svg-gold-royal-soft;
                  cursor: pointer;
                }
              }
            }
            /* ContentRow Title Icon */
            & > h1 > svg {
              height: calc(max(1.6666666667vw, 22px) * 1.167) !important;
              width: calc(max(1.6666666667vw, 22px) * 1.167) !important;
              filter: $--svg-summer-sun-mild;
              position: absolute;
              /* Hover */
              &:hover {
                filter: $--svg-summer-sun-dark;
                cursor: pointer;
              }
            }
            /* Collapse Area. Element In ContentRow  */
            .collapseArea {
              & > div > div > div > div {
                @extend .content;
                position: relative;
                margin: 2vh 0;
                /* Link Cards */
                &.card {
                  margin: 2vh 1vh;
                }
                /* Media Element In ContentRow */
                & {
                  /* Image Element In ContentRow */
                  & > img {
                    border: 2.5px ridge rgba(171, 150, 111, 1);
                    object-position: 20% 100%;
                    width: calc(100% - 5vw);
                    border-radius: 10px;
                    object-fit: cover;
                    margin: 0 2.5vw;
                  }
                  /* Video Element In ContentRow */
                  & > video {
                    border: 2.5px ridge rgba(171, 150, 111, 1);
                    object-position: 20% 100%;
                    width: calc(100% - 5vw);
                    border-radius: 10px;
                    object-fit: cover;
                    margin: 0 2.5vw;
                  }
                  /**/
                  & > svg {
                    filter: $--svg-orange-crushed-dark;
                    position: absolute;
                    height: 15%;
                    width: 15%;
                    /**/
                    &:hover {
                      filter: $--svg-orange-crushed-soft;
                      cursor: pointer;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
